    componentDidMount() {
      var wrapper = document.querySelector('.svgclass svg')
      
      function draw() {
        wrapper.classList.add('active')
      }
      function erase() {
        wrapper.classList.remove('active')
      }
      // Add handlers to our buttons
      setTimeout(()=>{
        draw();
      },300);
      setTimeout(()=>{
        erase(); 
      },2500);  // seconds it take to animate then erase
    }
    
    
